import React, { useEffect } from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { FooterBar, Input, LogoutBtn, Scrollable, Text } from '../../../../app/components'
import {
  DOBInput,
  EmailInput,
  LastNameInput,
  NameInput,
} from '../../../auth/pages/Profile/components'
import { Buttons } from './components'

import { UserIconBig } from '../images'

import { TEvents, TState } from '../../../../app/store1'

export const Profile: React.FC = () => {
  const intl = useIntl()

  const { dispatch, phone, name, lastName, DOB, email } = useStoreon<TState, TEvents>(
    'phone',
    'name',
    'lastName',
    'DOB',
    'email'
  )

  useEffect(() => {
    dispatch('on-profile-page-mount')
  }, [phone, name, lastName, DOB, email, dispatch])

  // @ts-ignore
  return (
    <>
      <Scrollable>
        <LogoutBtn />
        <div className='top-35 center'>
          <UserIconBig />
        </div>
        <div className='top-20 side-25'>
          <Text variant='h1'>
            <FormattedMessage id='profile__title' />
          </Text>
        </div>

        <NameInput />

        <LastNameInput />

        <DOBInput />

        <EmailInput />

        <div className='top-20 side-25'>
          <Input disabled label={intl.formatMessage({ id: 'profile__label__5' })} value={phone} />
        </div>
        <div className='top-20 side-25'>
          <Input label={intl.formatMessage({ id: 'profile__label__6' })} to='/profile/card' />
        </div>
      </Scrollable>

      <Buttons />

      <FooterBar selected='profile' />
    </>
  )
}
