import {
  authCheck,
  authDelete,
  authToken,
  giftAdd,
  giftChoice,
  giftIncoming,
  giftIncomingNew,
  giftOutgoing,
  giftPayByCard,
  mandarinpayCardBindings,
  productItems,
  section,
  smsConfirm,
  userAddressBook,
  userCardBindPayForm,
  userCardBindPayout,
  userGet,
  userUpdate,
} from './urls'

import { getToken, query, queryWithNoBaseUrl } from './utils'

import { TGiftOutgoingResponse } from '../../features/catalog1/types'

/* auth */
export const useGetToken = getToken
export const useSmsConfirmQuery = () => (phone: string) => query('get', smsConfirm, { phone })
export const useAuthTokenQuery = () => (phone: string, sms_code: string) =>
  query('get', authToken, { phone, sms_code })
export const useAuthCheckQuery = () => () => query('get', authCheck)
export const useAuthDeleteQuery = () => () => query('post', authDelete)

/* user */
export const useUserGetQuery = () => () => query('get', userGet)
export const useUserUpdateQuery = () => (data: {}) => query('post', userUpdate, undefined, data)

export const useGiftChoiceQuery = () => () => query('post', giftChoice)

export const useGiftIncomingQuery = () => (page: number) =>
  query('get', giftIncoming, { page: page.toString() })

export const useGiftIncomingNewQuery = () => () => query('get', giftIncomingNew)

export const useProductItemsQuery = () => (id: number) =>
  query('get', productItems, { f_id: id.toString() })

export const useUserCardBindPayForm = () => () => query('get', userCardBindPayForm)

export const useUserCardBindPayout = () => (cardNumber: string) =>
  query('post', userCardBindPayout, {
    card_number: cardNumber,
    payment_system: 'winpay',
  })

export const useGiftPayByCard = () => (giftId: number) =>
  query('post', giftPayByCard, undefined, { payment_system: 'mandarinpay', id: giftId.toString() })

type TDoneeData = {
  name: string
  last_name: string
  phone: string
  product_id: string
  message: string
}

const convertObjectToFormData = (obj: { [key: string]: string }) => {
  const fd = new FormData()
  Object.keys(obj).forEach((key) => fd.append(key, obj[key]))
  return fd
}

export const useGiftAdd = () => (data: TDoneeData) =>
  query('post', giftAdd, undefined, convertObjectToFormData(data))

export const useUserAddressBook = () => () => query('get', userAddressBook)

/* catalog */
export const useSectionQuery = () => () => query('get', section)

export const useMandarinpayCardBindings = () => () =>
  queryWithNoBaseUrl('post', mandarinpayCardBindings, undefined, {
    customerInfo: { email: ' user@example.com' },
  })

const queryIncomingGifts = () => query('get', giftIncoming)

const queryNewIncomingGifts = () => query('get', giftIncomingNew)

const queryProductInfo = (id: number) => query('get', productItems, { f_id: String(id) })

const querySentHistory = (): TGiftOutgoingResponse => query('get', giftOutgoing)

export const useSharedQueries = () => ({
  queryIncomingGifts,
  queryNewIncomingGifts,
  queryProductInfo,
  querySentHistory,
})
