import { createStoreon } from 'storeon'
import { storeonDevtools } from 'storeon/devtools'

import { authModel } from '../../features/auth/model'
import { onboardingModel } from '../../features/onboarding/model'
import { profileModel } from '../../features/profile/model'
import { appModel } from '../model'

export type TState = {
  authorized: boolean
  aboutModal: boolean
  popupMsg: string
  cbEvent: string
  redirectLink: string
  phone: string
  smsCode: string
  prevSmsCode: string
  timer: number
  name: string
  lastName: string
  DOB: string
  gender: 'male' | 'female' | ''
  email: string
}

export type TEvents = {
  'on-login': undefined
  'on-logout': undefined
  'on-about': undefined
  'on-about-close': undefined
  'on-popup-ok': undefined
  'on-popup-yes': undefined
  'on-popup-no': undefined
  'on-after-redirect': undefined
  'on-onboarding-skip': undefined
  'on-onboarding-got-it': undefined
  'on-auth-phone-mount-if-authorized': undefined
  'on-auth-code-mount-if-authorized': undefined
  'on-phone-change': string
  'on-auth-phone-continue': undefined
  'on-sms-code-change': string
  'on-timer-change': undefined
  'on-sms-code-send-repeatly': undefined
  'on-name-change': string
  'on-name-blur': undefined
  'on-last-name-change': string
  'on-last-name-blur': undefined
  'on-dob-change': string
  'on-gender-change': 'male' | 'female'
  'on-email-change': string
  'on-email-blur': undefined
  'on-auth-profile-continue': undefined
  'on-profile-continue': undefined
  'on-profile-page-mount': undefined
  'set-authorized': boolean
  'set-about-modal': boolean
  'set-popup-msg': string
  'set-cb-event': string
  'set-redirect-link': string
  'set-phone': string
  'set-sms-code': string
  'set-prev-sms-code': string
  'set-timer': number
  'set-name': string
  'set-last-name': string
  'set-dob': string
  'set-gender': 'male' | 'female'
  'set-email': string
  'query-sms-confirm': 'phone-page' | 'code-page'
  'query-auth-token': undefined
  'query-user-get': undefined
  'query-user-update': 'auth-profile' | 'profile'
}

export const store = createStoreon<TState, TEvents>([
  ...appModel,
  ...onboardingModel,
  ...authModel,
  ...profileModel,
  process.env.NODE_ENV !== 'production' && storeonDevtools,
])
