import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../app/components'

type TProps = {
  message: string
  onBack: () => void
  onChange: (val: string) => void
  onContinue: () => void
}

export const Message: React.FC<TProps> = ({ message, onBack, onChange, onContinue }) => {
  const intl = useIntl()

  return (
    <>
      <BackBtn onClick={onBack} />

      <div className='top-60 side-25'>
        <Text variant='h1'>
          <FormattedMessage id='contacts__message__title' />
        </Text>
      </div>

      <Scrollable>
        <div className='top-35 bottom-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__message__label' })}
            value={message}
            closeBtn
            onChange={onChange}
          />
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' disabled={message === ''} onClick={onContinue}>
          <FormattedMessage id='contacts__message__button' />
        </Button>
      </div>
    </>
  )
}
