import React, { useEffect } from 'react'
import { useStoreon } from 'storeon/react'

import { AboutBtn, Scrollable } from '../../../../app/components'
import { Logo } from '../../components'
import { Buttons, Description, SmsCodeInput, TimerHint, Title } from './components'

import { TEvents, TState } from '../../../../app/store1'

export const Code: React.FC = () => {
  const { dispatch, authorized } = useStoreon<TState, TEvents>('authorized')

  useEffect(() => {
    if (authorized) dispatch('on-auth-code-mount-if-authorized')
  }, [authorized, dispatch])

  return (
    <>
      <Scrollable>
        <AboutBtn />

        <Logo />

        <Title />

        <Description />

        <SmsCodeInput />

        <TimerHint />
      </Scrollable>

      <Buttons />
    </>
  )
}
