import { StoreonModule } from 'storeon'

import { queryAuthToken, querySmsConfirm } from './queries'

import { sendUserEventToGA } from '../../../app/googleAnalytics'
import { TEvents, TState } from '../../../app/store1'
import { setToken } from '../../../app/http/utils1'
import {
  validateAnyNameInput,
  validateEmail,
  validatePhoneInput,
  validateRequired,
  validateSmsCode,
  validateSmsCodeInput,
} from '../../../app/utils'

const authEvents: StoreonModule<TState, TEvents> = (store) => {
  store.on('on-auth-phone-mount-if-authorized', () =>
    store.dispatch('set-redirect-link', '/onboarding')
  )
  store.on('on-auth-code-mount-if-authorized', () =>
    store.dispatch('set-redirect-link', '/onboarding')
  )
  store.on('on-phone-change', async (state, phone) => {
    if (validatePhoneInput(phone)) {
      store.dispatch('set-phone', phone)
    }
  })
  store.on('on-auth-phone-continue', () => store.dispatch('query-sms-confirm', 'phone-page'))
  store.on('on-sms-code-change', (state, code) => {
    if (validateSmsCodeInput(code)) {
      store.dispatch('set-sms-code', code)
      store.dispatch('set-prev-sms-code', state.smsCode)
      if (validateSmsCode(code)) store.dispatch('query-auth-token')
    }
  })
  store.on('on-timer-change', (state) => store.dispatch('set-timer', state.timer - 1))
  store.on('on-sms-code-send-repeatly', () => store.dispatch('query-sms-confirm', 'code-page'))
  store.on('on-name-change', (state, name) => {
    if (validateAnyNameInput(name)) store.dispatch('set-name', name)
  })
  store.on('on-name-blur', (state) => {
    if (!validateRequired(state.name))
      store.dispatch('set-popup-msg', 'auth__profile__require_name_msg')
  })
  store.on('on-last-name-change', (state, lastName) => {
    if (validateAnyNameInput(lastName)) store.dispatch('set-last-name', lastName)
  })
  store.on('on-last-name-blur', (state) => {
    if (!validateRequired(state.lastName))
      store.dispatch('set-popup-msg', 'auth__profile__require_last_name_msg')
  })
  store.on('on-dob-change', (state, DOB) => store.dispatch('set-dob', DOB))
  store.on('on-gender-change', (state, gender) => store.dispatch('set-gender', gender))
  store.on('on-email-change', (state, email) => store.dispatch('set-email', email))
  store.on('on-email-blur', (state) => {
    if (!validateRequired(state.email))
      store.dispatch('set-popup-msg', 'auth__profile__require_email_msg')
    else if (!validateEmail(state.email))
      store.dispatch('set-popup-msg', 'auth__profile__invalid_email_msg')
  })
  store.on('on-auth-profile-continue', () => store.dispatch('query-user-update', 'auth-profile'))
  store.on('on-profile-continue', () => store.dispatch('query-user-update', 'profile'))
}

const authValues: StoreonModule<TState, TEvents> = (store) => {
  store.on('@init', () => ({
    phone: '',
    smsCode: '',
    prevSmsCode: '',
    timer: 300,
    name: '',
    lastName: '',
    DOB: '',
    gender: '',
    email: '',
  }))
  store.on('set-phone', (state, phone) => ({ phone }))
  store.on('set-sms-code', (state, smsCode) => ({ smsCode }))
  store.on('set-prev-sms-code', (state, prevSmsCode) => ({ prevSmsCode }))
  store.on('set-timer', (state, timer) => ({ timer }))
  store.on('set-name', (state, name) => ({ name }))
  store.on('set-last-name', (state, lastName) => ({ lastName }))
  store.on('set-dob', (state, DOB) => ({ DOB }))
  store.on('set-gender', (state, gender) => ({ gender }))
  store.on('set-email', (state, email) => ({ email }))
}

const authQueries: StoreonModule<TState, TEvents> = (store) => {
  store.on('query-sms-confirm', async (state, from) => {
    const { data } = await querySmsConfirm(state.phone)
    if (data.status) {
      if (from === 'phone-page') {
        sendUserEventToGA('Ввод телефона для регистрации')
        store.dispatch('set-redirect-link', '/auth/code')
      }
      if (from === 'code-page') store.dispatch('set-timer', 300)
    } else store.dispatch('set-popup-msg', data.message.text)
  })
  store.on('query-auth-token', async (state) => {
    const { data } = await queryAuthToken(state.phone, state.smsCode)
    if (data.status) {
      sendUserEventToGA('Успешная регистрация')
      if (data.user.is_new) store.dispatch('set-redirect-link', '/auth/profile')
      else store.dispatch('set-redirect-link', '/home')
      setToken(data.access_token)
      store.dispatch('set-authorized', true)
    }
  })
}

export const authModel: StoreonModule<TState, TEvents>[] = [authEvents, authValues, authQueries]
