import React, { useState } from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Button, CloseBtn, Input, Scrollable, Text } from '../../../../app/components'

import { useUserCardBindPayout } from '../../../../app/http'
import { TEvents, TState } from '../../../../app/store1'

type TProps = {
  on: (e: 'close' | 'successBind') => void
}

export const Card: React.FC<TProps> = ({ on }) => {
  const intl = useIntl()
  const query = useUserCardBindPayout()

  const { dispatch } = useStoreon<TState, TEvents>()

  const [cardNumber, setCardNumber] = useState('')

  const validateCardNumber = (val: string) => val.length === 16
  const checkNumberOnly = (val: string) => val.match(/[^\d]/g) === null
  const checkCardNumberLength = (val: string) => val.length <= 16

  const handleCardNumberInput = (val: string) => {
    if (checkNumberOnly(val) && checkCardNumberLength(val)) {
      setCardNumber(val)
    }
  }
  const handleContinueButtonClick = async () => {
    if (validateCardNumber(cardNumber)) {
      const { data } = await query(cardNumber)
      if (!data.status) dispatch('set-popup-msg', data.message.text)
      if (data.status) on('successBind')

      // eslint-disable-next-line no-console
      if (data.status) console.log(data)
      // eslint-disable-next-line no-console
      if (!data.status) console.log(data)
    }
  }
  const handleClearCardNumberClick = () => setCardNumber('')

  return (
    <>
      <CloseBtn onClick={() => on('close')} />

      <Scrollable>
        <div className='top-60 side-25'>
          <Text variant='h1'>
            <FormattedMessage id='home__card__title' />
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'home__card__label' })}
            closeBtn
            value={cardNumber}
            onChange={handleCardNumberInput}
            onClear={handleClearCardNumberClick}
          />
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button
          variant='filled'
          disabled={!validateCardNumber(cardNumber)}
          onClick={handleContinueButtonClick}
        >
          <FormattedMessage id='home__card__button' />
        </Button>
      </div>
    </>
  )
}
