import React from 'react'
import ReactDOM from 'react-dom'
import { IntlProvider } from 'react-intl'
import { BrowserRouter as Router } from 'react-router-dom'
import dayjs from 'dayjs'
import { StoreContext } from 'storeon/react'
import { ThemeProvider } from 'styled-components'

import { App } from './app'

import appLocales from './app/constants/dayjsLocales'
import { messages } from './app/lang'
import { StoreProvider } from './app/store'
import { store } from './app/store1'

import { GlobalStyles, theme } from './app/theme'

dayjs.locale(appLocales.ru)

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <IntlProvider messages={messages.ru} locale='ru'>
          <StoreProvider>
            <StoreContext.Provider value={store}>
              <App />
            </StoreContext.Provider>
          </StoreProvider>
        </IntlProvider>
      </ThemeProvider>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
)
