import React, { FC } from 'react'
import { useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'

export const EmailInput: FC = () => {
  const intl = useIntl()

  const { dispatch, email } = useStoreon<TState, TEvents>('email')

  return (
    <div className='top-20 side-25'>
      <Input
        label={intl.formatMessage({ id: 'auth__profile__label__4' })}
        onChange={(val) => dispatch('on-email-change', val)}
        value={email}
      />
    </div>
  )
}
