import React from 'react'
import { useHistory } from 'react-router-dom'

import { Button } from '..'
import { CloseIcon, CloseWhiteIcon } from './icons'

import { Wrap } from './styled'

type TProps = {
  white?: boolean
  onClick?: () => void
}

export const CloseBtn: React.FC<TProps> = ({ white: isWhite, onClick }) => {
  const { goBack } = useHistory()

  const handleClick = () => {
    if (onClick) onClick()
    else goBack()
  }

  return (
    <Wrap>
      <Button variant='icon' onClick={handleClick}>
        {isWhite ? <CloseWhiteIcon /> : <CloseIcon />}
      </Button>
    </Wrap>
  )
}
