// eslint-disable-file
import axios from 'axios'

import { generateAppSign, getToken } from './utils'

const Http = {
  get: (url: string, query: any = false) => {
    let prepUrl = url
    if (query) {
      prepUrl += `?${Object.keys(query).map((item) => `${item}=${query[item]}`)}`
    }
    return axios
      .create({
        baseURL: `https://api.magifta.com/api/v1`,
        timeout: 15000,
        headers: {
          'app-sign': generateAppSign(),
          'access-token': getToken(),
        },
      })
      .get(prepUrl)
  },
  post: (url: string, data: any) => {
    return axios
      .create({
        baseURL: `https://api.magifta.com/api/v1`,
        timeout: 15000,
        headers: {
          'app-sign': generateAppSign(),
          'access-token': getToken(),
        },
      })
      .post(url, data)
  },
}

export { Http }
