import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../app/components'

import { demaskPhoneNum, maskPhoneNum } from '../../../app/utils'

type TProps = {
  giftName: string
  contactName: string
  phone: string
  message: string
  price?: number
  onBack: () => void
  onContactNameChange: (val: string) => void
  onPhoneChange: (val: string) => void
  onMessageChange: (val: string) => void
  onPay: () => void
}

export const Confirmation: React.FC<TProps> = ({
  giftName,
  contactName,
  phone,
  message,
  price,
  onBack,
  onContactNameChange,
  onPhoneChange,
  onMessageChange,
  onPay,
}) => {
  const intl = useIntl()

  return (
    <>
      <BackBtn onClick={onBack} />

      <div className='top-60 side-25'>
        <Text variant='h1'>
          <FormattedMessage id='contacts__confirmation__title' />
        </Text>
      </div>

      <Scrollable>
        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__1' })}
            value={giftName}
            disabled
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__2' })}
            value={contactName}
            onChange={onContactNameChange}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            variant='phoneMini'
            label={intl.formatMessage({ id: 'contacts__confirmation__label__3' })}
            value={maskPhoneNum(phone)}
            onChange={(val) => onPhoneChange(demaskPhoneNum(val))}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__4' })}
            value={message}
            onChange={onMessageChange}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__5' })}
            value={`${price}`}
            disabled
          />
        </div>

        <div className='top-35 side-25'>
          <Text variant='h3' color='black'>
            <FormattedMessage id='contacts__confirmation__subtitle' />
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Text variant='field' color='neutral'>
            {`Мы зарезервируем ${price} ₽  на вашей карте и  спишем после получения подарка`}
          </Text>
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' onClick={onPay}>{`Оплатить ${price} ₽`}</Button>
      </div>
    </>
  )
}
