import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import styled from 'styled-components'

import { Button, CloseBtn, Scrollable, SliderDefault, Text } from '../../../app/components'

import { useSendUserEventToGoogleAnalytics } from '../../../app/googleAnalytics'
import { useStore } from '../../../app/store'
import { Http } from '../../../app/http/services'

const Span = styled.div`
  display: inline-block;
  margin-left: 4px;
  font-family: Manrope200, Helvetica, Arial, sans-serif;
`

type TProduct = {
  name: string | undefined
  description: string
  price: number | undefined
  gallery: string[] | undefined
}

type TProps = {
  onClose: () => void
  productId: number
  onGift: () => void
}

export const Details: React.FC<TProps> = ({ onClose, productId, onGift }) => {
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()

  const {
    dispatch,
    actions: { setCurrentGift, setDoneePhone, setDoneeName, setDoneeMessage },
  } = useStore()
  const [product, setProduct] = useState<TProduct>({
    name: undefined,
    description: '',
    price: undefined,
    gallery: undefined,
  })

  useEffect(() => {
    ;(async () => {
      dispatch(
        setCurrentGift({
          price: '',
          name: '',
          id: 0,
        })
      )
      dispatch(setDoneeMessage(''))
      dispatch(setDoneeName(''))
      dispatch(setDoneePhone(''))
      const {
        data: { items },
      } = await Http.get('/product/items/', { f_id: productId })
      const {
        name,
        description,
        price: { initial },
        gallery,
      } = items[0]
      setProduct({ name, description, price: initial, gallery })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const handleGiftBtnClick = () => {
    sendUserEvent('Нажата кнопка подарить')
    dispatch(
      setCurrentGift({
        price: product.price || '',
        name: product.name || '',
        id: productId,
      })
    )
    onGift()
  }

  return (
    <>
      <CloseBtn white onClick={onClose} />

      <Scrollable>
        {product.gallery ? (
          <SliderDefault
            images={product.gallery.map((image, index) => ({ id: index, image }))}
            dotsColor='blue'
          />
        ) : null}

        <div className='top-20 side-25'>
          <Text variant='h3' color='gray1'>
            {product.name}
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Text variant='h3' color='purple'>
            {product.price}
            <Span color='purple'>
              <FormattedMessage id='catalog__card__price' />
            </Span>
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Text variant='description' color='black'>
            {/* eslint-disable-next-line react/no-danger */}
            <span dangerouslySetInnerHTML={{ __html: product.description }} />
          </Text>
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' onClick={handleGiftBtnClick}>
          <FormattedMessage id='catalog__card__button' />
        </Button>
      </div>
    </>
  )
}
