import React, { Fragment } from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { Button, CloseBtn, ContactsItem, Input, Scrollable, Text } from '../../../app/components'

type TContact = {
  id: number
  phone: string
  name: string
  photo: boolean | string
}

type TProps = {
  contacts: TContact[]
  doneeName: string
  onClose: () => void
  onSearch: (val: string) => void
  onSelect: (id: number) => void
  onContinue: () => void
}

export const Contacts: React.FC<TProps> = ({
  contacts,
  doneeName,
  onClose,
  onSearch,
  onSelect,
  onContinue,
}) => {
  const intl = useIntl()

  return (
    <>
      <CloseBtn onClick={onClose} />

      <div className='top-60 side-25'>
        <Text variant='h1'>
          <FormattedMessage id='contacts__title' />
        </Text>
      </div>

      <div className='top-35 bottom-20 side-25'>
        <Input
          label={intl.formatMessage({
            id: 'contacts__label',
          })}
          closeBtn
          value={doneeName}
          onChange={onSearch}
        />
      </div>

      <Scrollable>
        <div className='side-25'>
          {contacts.filter(({ name }) => name.includes(doneeName)).length
            ? contacts
                .sort((a, b) => {
                  if (a.name < b.name) return -1
                  if (a.name > b.name) return 1
                  return 0
                })
                .filter(({ name }) => name.includes(doneeName))
                .map((item, index, arr) => {
                  const { name, phone, photo, id } = item
                  const isNewLetter =
                    index === 0 ||
                    name.substr(0, 1).toLowerCase() !==
                      arr[index - 1].name.substr(0, 1).toLowerCase()
                  return (
                    <Fragment key={id}>
                      {isNewLetter && (
                        <div className='bottom-20'>
                          <Text variant='h4'>{name.substr(0, 1)}</Text>
                        </div>
                      )}

                      <ContactsItem
                        name={name}
                        phone={phone}
                        photo={photo}
                        onClick={() => onSelect(id)}
                      />
                    </Fragment>
                  )
                })
            : 'Контактов не найдено'}
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' disabled={doneeName === ''} onClick={onContinue}>
          <FormattedMessage id='contacts__button' />
        </Button>
      </div>
    </>
  )
}
