import React from 'react'
import ReactModal from 'react-modal'
import { useStoreon } from 'storeon/react'

import { OkBtn, Title, YesNoButtons } from './components'

import { TEvents, TState } from '../../store1'

ReactModal.setAppElement('#root')

export const Popup: React.FC = () => {
  const { popupMsg, cbEvent } = useStoreon<TState, TEvents>('popupMsg', 'cbEvent')

  return (
    <ReactModal
      isOpen={popupMsg !== ''}
      style={{
        content: {
          top: '50%',
          left: '50%',
          right: 'auto',
          bottom: 'auto',
          transform: 'translate(-50%, -50%)',
          minWidth: 300,
          padding: '20px 25px 0',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'stretch',
        },
        overlay: {
          zIndex: 1000,
        },
      }}
    >
      <Title />

      {cbEvent === '' ? <OkBtn /> : <YesNoButtons />}
    </ReactModal>
  )
}
