import React from 'react'
import { FormattedMessage } from 'react-intl'

import {
  Button,
  CloseBtn,
  Scrollable,
  SliderDefault,
  Space,
  Text,
} from '../../../../app/components'

import { ImgPlaceholder } from '../ImgPlaceholder'

import { TIncomingGift } from '../../../../app/types'

import { Message } from './styled'

type TProps = {
  gift: TIncomingGift
  on: (e: 'close' | 'gift' | 'money') => void
}

export const Receive: React.FC<TProps> = ({ gift, on }) => {
  const {
    product: {
      items: {
        0: { gallery, name: giftName },
      },
    },
    who: { name: whoName },
    message,
    price: { initial: price },
  } = gift

  return (
    <>
      <CloseBtn onClick={() => on('close')} white />

      <Scrollable>
        <SliderDefault images={gallery || [{ image: ImgPlaceholder, id: 0 }]} dotsColor='blue' />

        <div className='top-20 side-25'>
          <Text variant='h3'>{giftName}</Text>
        </div>

        <div className='top-20 side-25'>
          <Message>
            <Text variant='description' color='purple'>
              <Text variant='descriptionBold' color='purple'>
                {whoName}
              </Text>

              {message}
            </Text>
          </Message>
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='outlined' onClick={() => on('gift')}>
          <FormattedMessage id='receive__receive__button__1' />
        </Button>

        <Space size={10} />

        <Button variant='filled' onClick={() => on('money')}>
          <FormattedMessage id='receive__receive_money' values={{ price }} />
        </Button>
      </div>
    </>
  )
}
