import { StoreonModule } from 'storeon'

import { sendUserEventToGA } from '../googleAnalytics'
import { queryUserUpdate } from '../http/http'
import { TEvents, TState } from '../store1'

const appEvents: StoreonModule<TState, TEvents> = (store) => {
  store.on('on-login', () => store.dispatch('set-authorized', true))
  store.on('on-logout', () => store.dispatch('set-authorized', false))
  store.on('on-about', () => store.dispatch('set-about-modal', true))
  store.on('on-about-close', () => store.dispatch('set-about-modal', false))
  store.on('on-popup-ok', () => store.dispatch('set-popup-msg', ''))
  store.on('on-after-redirect', () => store.dispatch('set-redirect-link', ''))
}

const appValues: StoreonModule<TState, TEvents> = (store) => {
  store.on('@init', () => ({
    authorized: false,
    aboutModal: false,
    popupMsg: '',
    cbEvent: '',
    redirectLink: '',
  }))
  store.on('set-authorized', (state, authorized) => ({ authorized }))
  store.on('set-about-modal', (state, aboutModal) => ({ aboutModal }))
  store.on('set-popup-msg', (state, popupMsg) => ({ popupMsg }))
  store.on('set-cb-event', (state, cbEvent) => ({ cbEvent }))
  store.on('set-redirect-link', (state, redirectLink) => ({ redirectLink }))
}

const appQueries: StoreonModule<TState, TEvents> = (store) => {
  store.on('query-user-update', async (state, from) => {
    const { data } = await queryUserUpdate({
      name: state.name,
      last_name: state.lastName,
      email: state.email,
      ...(state.DOB !== '' && { birthday: state.DOB }),
      ...(state.gender !== '' && { gender: state.gender }),
    })
    if (data.status) {
      if (data.result.failed.lenght > 0)
        store.dispatch('set-popup-msg', data.result.failed.values.join('. '))
      else if (from === 'auth-profile') {
        sendUserEventToGA('Профиль успешно заполнен')
        store.dispatch('set-popup-msg', 'Сохранено!')
        store.dispatch('set-redirect-link', '/home')
      } else if (from === 'profile') store.dispatch('set-popup-msg', 'Сохранено')
    } else store.dispatch('set-popup-msg', data.message.text)
  })
}

export const appModel: StoreonModule<TState, TEvents>[] = [appEvents, appValues, appQueries]
