import React, { useState } from 'react'

import { Button } from '..'
import { Chevron, CloseIcon } from './icons'

import { InputWrap, Wrap } from './styled'

type TProps = {
  variant?: 'phone' | 'sms-code' | 'phoneMini' | 'date'
  label?: string
  fullWidth?: boolean
  to?: string
  closeBtn?: boolean
  top?: number
  readOnly?: boolean
  value?: string
  onChange?: (value: string) => void
  onFocusChange?: (value: boolean) => void
  disabled?: boolean
  onClear?: () => void
}

export const Input: React.FC<TProps> = ({
  variant,
  label,
  fullWidth,
  to,
  closeBtn,
  top,
  value,
  readOnly,
  onChange,
  onFocusChange,
  disabled,
  onClear,
}) => {
  const inputMode = variant === 'phone' || variant === 'sms-code' ? 'numeric' : undefined

  const computePlaceholder = () => {
    if (variant === 'phoneMini') return '(9__) ___-__-__'
    if (variant === 'phone') return '(9__) ___-__-__'
    if (variant === 'sms-code') return '_'
    return undefined
  }
  const computeSize = () => {
    if (variant === 'phoneMini') return 18
    if (variant === 'phone') return 18
    if (variant === 'sms-code') return undefined
    return undefined
  }
  const computeClassName = () => {
    if (variant === 'phone') return 'phone-input'
    if (variant === 'sms-code') return 'sms-code-input'
    return undefined
  }

  const [isFocused, setIsFocused] = useState(false)
  const [input, setInput] = useState(value)

  const handleChange = (val: string) => {
    setInput(val)
    if (onChange) onChange(val)
  }
  const handleFocus = (val: boolean) => {
    setIsFocused(val)
    if (onFocusChange) onFocusChange(val)
  }

  return (
    <Wrap
      variant={variant}
      fullWidth={fullWidth}
      isFocus={isFocused}
      isEmpty={input === ''}
      top={top}
    >
      {label && <label htmlFor={label}>{label}</label>}

      <InputWrap variant={variant}>
        {(variant === 'phone' || variant === 'phoneMini') && <span>+7</span>}
        <input
          id={label}
          size={computeSize()}
          type={variant === 'date' ? 'date' : 'text'}
          readOnly={readOnly}
          placeholder={computePlaceholder()}
          disabled={to !== undefined || disabled}
          onChange={(e) => handleChange(e.target.value)}
          onFocus={() => handleFocus(true)}
          onBlur={() => handleFocus(false)}
          value={value}
          inputMode={inputMode}
          autoComplete='off'
          className={computeClassName()}
        />

        {to && (
          <Button variant='icon' to={to}>
            <Chevron />
          </Button>
        )}

        {closeBtn && value !== '' && value !== undefined && (
          <Button variant='icon' onClick={onClear}>
            <CloseIcon />
          </Button>
        )}
      </InputWrap>
    </Wrap>
  )
}
