import React, { FC } from 'react'
import { FormattedMessage } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Button } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { validateAnyName, validateEmail } from '../../../../../../app/utils'

export const Buttons: FC = () => {
  const { dispatch, name, lastName, email } = useStoreon<TState, TEvents>(
    'name',
    'lastName',
    'email'
  )

  return (
    <div className='top-20 bottom-20 side-25'>
      <Button
        variant='filled'
        disabled={!validateAnyName(name) || !validateAnyName(lastName) || !validateEmail(email)}
        onClick={() => dispatch('on-profile-continue')}
      >
        <FormattedMessage id='auth__profile__button' />
      </Button>
    </div>
  )
}
