import React, { FC } from 'react'
import { useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'

export const NameInput: FC = () => {
  const intl = useIntl()

  const { dispatch, name } = useStoreon<TState, TEvents>('name')

  return (
    <div className='top-20 side-25'>
      <Input
        label={intl.formatMessage({ id: 'auth__profile__label__1' })}
        onChange={(val) => dispatch('on-name-change', val)}
        value={name}
      />
    </div>
  )
}
