import React, { useEffect } from 'react'
import { pipe } from 'ramda'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import {
  demaskPhoneNum,
  getPhoneWithCode,
  getPhoneWithoutCode,
  maskPhoneNum,
} from '../../../../../../app/utils'

export const PhoneInput: React.FC = () => {
  const { dispatch, phone } = useStoreon<TState, TEvents>('phone')

  useEffect(() => {
    const input = document.getElementsByClassName('phone-input') as HTMLCollectionOf<
      HTMLInputElement
    >
    input[0].focus()
  }, [phone])

  return (
    <div className='top-20 center'>
      <Input
        variant='phone'
        onChange={(val) => dispatch('on-phone-change', pipe(demaskPhoneNum, getPhoneWithCode)(val))}
        value={pipe(getPhoneWithoutCode, maskPhoneNum)(phone)}
      />
    </div>
  )
}
