import { query } from './utils1'

const authCheck = 'auth/check/'
const userGet = 'user/get/'
const userUpdate = 'user/update/'

export const queryAuthCheck = () => query('get', authCheck)

export const queryUserGet = () => query('get', userGet)

export const queryUserUpdate = (data: { [key: string]: string }) =>
  query('post', userUpdate, undefined, data)
