import { query } from '../../../app/http'

import { TAddressBookResponse, TGift, TGiftAddResponse, TGiftPayByCardResponse } from '../types'

const section = 'section/'
const productItems = 'product/items/'
const userAddressBook = 'user/address_book/'
const giftAdd = 'gift/add/'
const giftPayByCard = 'gift/pay_by_card/'

const querySections = () => query('get', section)

const queryProductsBySectionId = (id: number) =>
  query('get', productItems, { section_id: String(id) })

const queryAddressBook = (): TAddressBookResponse => query('get', userAddressBook)

const convertObjectToFormData = (obj: { [key: string]: string | number }) => {
  const fd = new FormData()
  Object.keys(obj).forEach((key) => fd.append(key, String(obj[key])))
  return fd
}

const queryGiftAdd = (data: TGift): TGiftAddResponse =>
  query('post', giftAdd, undefined, convertObjectToFormData(data))

const queryGiftPayByCard = (giftId: number): TGiftPayByCardResponse =>
  query(
    'post',
    giftPayByCard,
    undefined,
    convertObjectToFormData({ payment_system: 'mandarinpay', id: String(giftId) })
  )

export const useQueries = () => ({
  querySections,
  queryProductsBySectionId,
  queryAddressBook,
  queryGiftAdd,
  queryGiftPayByCard,
})
