import React from 'react'
import { FormattedMessage } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Button } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { validatePhone } from '../../../../../../app/utils'

export const Buttons: React.FC = () => {
  const { dispatch, phone } = useStoreon<TState, TEvents>('phone')

  return (
    <div className='top-20 bottom-20 side-25'>
      <Button
        variant='filled'
        disabled={!validatePhone(phone)}
        onClick={() => dispatch('on-auth-phone-continue')}
      >
        <FormattedMessage id='auth__phone__button' />
      </Button>
    </div>
  )
}
