import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { useHistory } from 'react-router-dom'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../../app/components'

import { useSendUserEventToGoogleAnalytics } from '../../../../app/googleAnalytics'
import { useStore } from '../../../../app/store'

export const Message: React.FC = () => {
  const intl = useIntl()
  const redirect = useHistory().push
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()

  const {
    state: { doneeMessage },
    dispatch,
    actions: { setDoneeMessage },
  } = useStore()

  const handleContinueBtnClick = () => {
    sendUserEvent('Заполнен текст поздравления')
    redirect('/gift/confirmation')
  }

  return (
    <>
      <div className='top-60'>
        <BackBtn />
      </div>
      <Scrollable>
        <div className='side-25'>
          <Text variant='h1'>
            <FormattedMessage id='contacts__message__title' />
          </Text>
        </div>
        <div className='top-35 bottom-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__message__label' })}
            closeBtn
            value={doneeMessage}
            onChange={(val) => dispatch(setDoneeMessage(val))}
          />
        </div>
      </Scrollable>
      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' disabled={doneeMessage === ''} onClick={handleContinueBtnClick}>
          <FormattedMessage id='contacts__message__button' />
        </Button>
      </div>
    </>
  )
}
