import React from 'react'

import { Text } from '..'
import { Hourglass } from '../icons'

import { FormatPrice } from '../../utils'

import { Span, Wrap } from './styled'

type TProps = {
  giftName: string
  message: string
  senderName: string
  img: string
  giftPrice?: number
}

export const HistoryItem: React.FC<TProps> = ({
  giftName,
  message,
  senderName,
  giftPrice,
  img,
}) => (
  <Wrap>
    <div className='history-item-image'>
      <img src={img} alt='' />
    </div>
    <div className='history-item-description right-side-25'>
      <div className='history-item-right-top'>
        <div className='history-item-title bottom-20'>
          <Text variant='title'>{giftName}</Text>
        </div>
        {giftPrice && (
          <div className='history-item-price bottom-20'>
            <Text variant='title' color='purple'>
              {FormatPrice(giftPrice)}
              <Span color='purple'>₽</Span>
            </Text>
          </div>
        )}
      </div>
      <div className='history-item-right-bottom'>
        <div className='history-item-message'>
          <Text variant='description' color='gray3'>
            {senderName} → {message}
          </Text>
        </div>
        <div className='history-item-message-icon'>
          <Hourglass />
        </div>
      </div>
    </div>
  </Wrap>
)
