import React, { Suspense, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useStoreon } from 'storeon/react'

import { About, FullModal, Popup, Preloader } from './components'

import { initGoogleAnalytics } from './googleAnalytics'
import { queryAuthCheck } from './http/http'
import { Routes } from './routes'
import { TEvents, TState } from './store1'

import { View } from './styled'

initGoogleAnalytics()

export const App: React.FC = () => {
  const redirectTo = useHistory().push

  const { dispatch, authorized, aboutModal, redirectLink, popupMsg } = useStoreon<TState, TEvents>(
    'authorized',
    'aboutModal',
    'popupMsg',
    'redirectLink',
    'popupMsg'
  )

  /* const [height, setHeight] = useState(window.innerHeight) */
  /* useEffect(() => {
    const listener = () => setHeight(window.innerHeight)
    window.addEventListener('resize', listener)
    return () => window.removeEventListener('resize', listener)
  }) */

  useEffect(() => {
    if (redirectLink !== '') {
      redirectTo(redirectLink)
      dispatch('on-after-redirect')
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [redirectLink])

  useEffect(() => {
    if (authorized)
      (async () => {
        const { data } = await queryAuthCheck()

        if (!data.status) {
          dispatch('on-logout')
          redirectTo('/onboarding')
        }
      })()
    if (!authorized) redirectTo('/onboarding')
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [authorized])

  return (
    <View height={window.innerHeight}>
      <Suspense fallback={<Preloader />}>
        <Routes />

        <FullModal isOpen={aboutModal}>
          <About />
        </FullModal>

        {popupMsg !== '' && <Popup />}
      </Suspense>
    </View>
  )
}
