import React, { FC } from 'react'
import { useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'

export const LastNameInput: FC = () => {
  const intl = useIntl()

  const { dispatch, lastName } = useStoreon<TState, TEvents>('lastName')

  return (
    <div className='top-20 side-25'>
      <Input
        label={intl.formatMessage({ id: 'auth__profile__label_last_name' })}
        onChange={(val) => dispatch('on-last-name-change', val)}
        value={lastName}
      />
    </div>
  )
}
