import { query } from '../../../../app/http/utils1'

const smsConfirm = 'sms/confirm/'
const authToken = 'auth/token/'

export const querySmsConfirm = (phone: string) => query('get', smsConfirm, { phone })

export const queryAuthToken = (phone: string, sms_code: string) =>
  query('get', authToken, { phone, sms_code })
