import React, { useRef, useState } from 'react'
import Slider from 'react-slick'
import { nanoid } from 'nanoid'

import { Buttons, SliderDots, SliderText } from './components'

import img1 from './images/img1.svg'
import img2 from './images/img2.svg'
import img3 from './images/img3.svg'
import img4 from './images/img4.svg'

import { SliderWrap } from './styled'

export const Onboarding: React.FC = () => {
  const [index, setIndex] = useState<0 | 1 | 2>(0)

  const imgSlider = useRef<Slider>(null)
  const textSlider = useRef<Slider>(null)

  return (
    <>
      <SliderWrap>
        <Slider
          ref={imgSlider}
          asNavFor={textSlider.current as Slider}
          infinite={false}
          slidesToShow={2}
          slidesToScroll={1}
          arrows={false}
          afterChange={(idx: 0 | 1 | 2): void => setIndex(idx)}
        >
          {[img1, img2, img3, img4].map((img) => (
            <img key={nanoid()} src={img} alt='img' />
          ))}
        </Slider>
      </SliderWrap>

      <Slider
        ref={textSlider}
        asNavFor={imgSlider.current as Slider}
        infinite={false}
        slidesToShow={1}
        slidesToScroll={1}
        arrows={false}
        afterChange={(idx: 0 | 1 | 2): void => setIndex(idx)}
      >
        {[1, 2, 3].map((idx) => (
          <SliderText key={idx} idx={idx} />
        ))}
      </Slider>

      <SliderDots idx={index + 1} />

      <Buttons index={index} onNextClick={() => imgSlider.current?.slickNext()} />
    </>
  )
}
