import React, { useEffect } from 'react'
import { FormattedMessage } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Text } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { formatTimer } from './utils'

export const TimerHint: React.FC = () => {
  const { dispatch, timer } = useStoreon<TState, TEvents>('timer')

  useEffect(() => {
    const timeout = setTimeout(() => {
      if (timer > 0) dispatch('on-timer-change')
    }, 1000)
    return () => {
      clearTimeout(timeout)
    }
  })

  return (
    <div className='side-25'>
      <Text variant='description' color='gray3' center>
        <FormattedMessage
          id={timer === 0 ? 'auth__code__hint_time_is_out' : 'auth__code__hint'}
          values={{ time: formatTimer(timer) }}
        />
      </Text>
    </div>
  )
}
