import { phoneCode } from '../../../app/constants'

export const mask = (phoneNum: string, placeholder: string): string => {
  const { length } = phoneNum

  const splitPlaceholder: string[] = placeholder.split('')
  const splitPhoneNum: string[] = phoneNum.split('')

  let i = 0

  const masked = splitPlaceholder
    .map((char) => {
      if (char.match(/[() -]/i) !== null) return i < length ? char : ''
      if (i < length) {
        const newChar = splitPhoneNum[i]
        i += 1
        return newChar
      }
      return ''
    })
    .join('')

  return masked !== placeholder ? masked : ''
}

export const demask = (masked: string): string => masked.replace(/[()_-]/gi, '')

export const phoneWithCode = (phoneNum: string) => `${phoneCode}${phoneNum}`

export const validatePhone = (phone: string) => phone.length === 10

export const validateSmsCode = (code: string) => code.length === 4

export const cutPhone = (phone: string) => phone.substring(1)
