import React from 'react'
import { FormattedMessage } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Button } from '../../../../app/components'

type TProps = {
  index: 0 | 1 | 2
  onNextClick: () => void
}

export const Buttons: React.FC<TProps> = ({ index, onNextClick }) => {
  const { dispatch } = useStoreon()

  return (
    <>
      {(index === 0 || index === 1) && (
        <div className='top-20 bottom-20 side-25 center-between'>
          <Button variant='text' onClick={() => dispatch('on-onboarding-skip')}>
            <FormattedMessage id='onboarding__skip_button' />
          </Button>

          <Button variant='rounded' onClick={onNextClick}>
            -&gt;
          </Button>
        </div>
      )}

      {index === 2 && (
        <div className='side-25 top-20 bottom-20'>
          <Button variant='filled' onClick={() => dispatch('on-onboarding-got-it')}>
            <FormattedMessage id='onboarding__got_it_button' />
          </Button>
        </div>
      )}
    </>
  )
}
