import React from 'react'
import { pipe } from 'ramda'

import { CloseBtn, Input, Scrollable, Text } from '../../../app/components'
import { CompletedIcon } from '../../../app/components/icons'

import { getPhoneWithoutCode, maskPhoneNum } from '../../../app/utils'

type TProps = {
  giftName: string
  contactName: string
  phone: string
  message: string
  price?: number
  dateAndTime: string
  onClose: () => void
}

export const Result: React.FC<TProps> = ({
  giftName,
  contactName,
  phone,
  message,
  price,
  dateAndTime,
  onClose,
}) => {
  const transformPhoneNum = pipe(getPhoneWithoutCode, maskPhoneNum)

  return (
    <>
      <CloseBtn onClick={onClose} />

      <Scrollable>
        <div className='top-60 center'>
          <CompletedIcon />
        </div>

        <div className='top-20 side-25'>
          <Text variant='h1' color='green2' center>
            Исполнено!
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Input label='подарок' disabled value={giftName} />
        </div>

        <div className='top-20 side-25'>
          <Input label='имя получателя' disabled value={contactName} />
        </div>

        <div className='top-20 side-25'>
          <Input
            variant='phoneMini'
            label='номер телефона получателя'
            disabled
            value={transformPhoneNum(phone)}
          />
        </div>

        <div className='top-20 side-25'>
          <Input label='сообщение' disabled value={message} />
        </div>

        <div className='top-20 side-25'>
          <Input label='сумма списания' disabled value={String(price)} />
        </div>

        <div className='top-20 side-25 bottom-20'>
          <Input label='дата и время' disabled value={dateAndTime} />
        </div>
      </Scrollable>
    </>
  )
}
