import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import { nanoid } from 'nanoid'

import { Code, Phone, Profile } from './pages'

export const Auth: React.FC = () => (
  <Switch>
    <Route key={nanoid()} path='/auth/phone' component={Phone} />
    <Route key={nanoid()} path='/auth/code' component={Code} />
    <Route key={nanoid()} path='/auth/profile' component={Profile} />
    <Route key={nanoid()} path='/auth'>
      <Redirect to='/auth/phone' />
    </Route>
  </Switch>
)
