import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'

import { TRedirect, TRoute } from '../../types'

type TProps = {
  routes: TRoute[]
  redirect: TRedirect
}

export const SwitchRoutes: React.FC<TProps> = ({
  routes,
  redirect: { path: rPath, id: rId, redirect },
}) => (
  <Switch>
    {routes.map(({ id, path, view }) => (
      <Route path={path} component={view} key={id} />
    ))}
    <Route exact path={rPath} key={rId}>
      <Redirect to={redirect} />
    </Route>
  </Switch>
)
