import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { useHistory } from 'react-router-dom'
import Slider from 'react-slick'
import { useStoreon } from 'storeon/react'

import { AboutBtn, FooterBar, FullModal, Scrollable, Text } from '../../app/components'
import { Card, Logo, Money, Processing, Receive, SliderDots } from './components'

import { GiftWithStarsImage } from './images'
import { useQueries } from './model/queries'

import { useSendUserEventToGoogleAnalytics } from '../../app/googleAnalytics'
import { useSharedQueries } from '../../app/http'
import { useStore } from '../../app/store'
import { TEvents, TState } from '../../app/store1'

import { TIncomingGift } from '../../app/types'

import { Banner, SliderWrap } from './styled'

export const Home: React.FC = () => {
  const {
    state: {
      authorized,
      newIncomingGifts: { count, items },
    },
    dispatch,
    actions: { setNewIncomingGifts },
  } = useStore()

  const { dispatch: dispatchStoreon } = useStoreon<TState, TEvents>()

  const { queryIncomingGifts } = useSharedQueries()
  const { queryMoney, queryGift } = useQueries()

  const redirect = useHistory().push
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()

  const [index, setIndex] = useState(0)
  const [fullModal, setFullModal] = useState<null | 'receive' | 'processing' | 'money' | 'card'>(
    null
  )
  const [currentGiftId, setCurrentGiftId] = useState<number | null>(null)
  const [currentGift, setCurrentGift] = useState<TIncomingGift | null>(null)

  const getGiftById = (currentId: number, arr: TIncomingGift[]) =>
    arr.find(({ id }) => currentId === id)

  const isIncomingItems = count !== 0

  useEffect(() => {
    if (currentGiftId !== null) {
      const gift = getGiftById(currentGiftId, items)
      if (gift) setCurrentGift(gift)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentGiftId])

  const handleGetGiftBannerClick = async (id: number) => {
    sendUserEvent('Переход на получение подарка')
    await setCurrentGiftId(id)
    setFullModal('receive')
  }
  const handleChoiceGiftBannerClick = () => {
    sendUserEvent('Клик на баннер выбрать подарок')
    redirect('/catalog')
  }
  const handleReceiveModalEvent = async (e: 'close' | 'gift' | 'money') => {
    if (e === 'close') setFullModal(null)
    if (e === 'gift') {
      sendUserEvent('Переход на получение подарка (доставка)')
      const { data } = await queryGift(currentGiftId || 0)
      // eslint-disable-next-line no-console
      console.log(data)
      if (data.status) setFullModal('processing')
      if (!data.status) dispatchStoreon('set-popup-msg', data.message.text)
    }
    if (e === 'money') {
      sendUserEvent('Переход на получение денег')
      const { data } = await queryMoney(currentGiftId || 0)
      if (data.status) setFullModal('money')
      if (!data.status) dispatchStoreon('set-popup-msg', data.message.text)
    }
    if (authorized) {
      const { data } = await queryIncomingGifts()
      if (data.status) dispatch(setNewIncomingGifts(data.items, data.items_count))
    }
  }
  const handleProcessingModalEvent = (e: 'close') => {
    if (e === 'close') {
      setFullModal(null)
      setCurrentGiftId(null)
    }
  }
  const handleMoneyModalEvent = (e: 'close') => {
    if (e === 'close') setFullModal(null)
  }
  const handleCardBindModalEvent = (e: 'close' | 'successBind') => {
    if (e === 'close') setFullModal(null)
  }

  return (
    <>
      <FullModal isOpen={fullModal !== null}>
        {fullModal === 'receive' && currentGift !== null && (
          <Receive gift={currentGift} on={handleReceiveModalEvent} />
        )}

        {fullModal === 'processing' && currentGift !== null && (
          <Processing gift={currentGift} on={handleProcessingModalEvent} />
        )}

        {fullModal === 'money' && currentGift !== null && (
          <Money gift={currentGift} on={handleMoneyModalEvent} />
        )}

        {fullModal === 'card' && <Card on={handleCardBindModalEvent} />}
      </FullModal>

      <Scrollable>
        <AboutBtn />

        <Logo />

        <div className='top-20 grow full-width'>
          <SliderWrap>
            <Slider
              infinite={false}
              slidesToShow={1}
              slidesToScroll={1}
              arrows={false}
              afterChange={(idx: number) => setIndex(idx)}
            >
              {isIncomingItems &&
                items.map(({ id, who: { name } }) => (
                  <div className='side-25' key={id}>
                    <Banner color='orange' onClick={() => handleGetGiftBannerClick(id)}>
                      <GiftWithStarsImage />

                      <div>
                        <div className='top-20'>
                          <Text variant='h1' color='white' center>
                            <FormattedMessage id='home__receive__title' />
                          </Text>
                        </div>

                        <div className='top-20'>
                          <Text variant='h4' center>
                            <FormattedMessage id='home__receive__description' values={{ name }} />
                          </Text>
                        </div>
                      </div>

                      <Text variant='button' color='white'>
                        <FormattedMessage id='home__receive__button' />
                      </Text>
                    </Banner>
                  </div>
                ))}

              <div className='side-25'>
                <Banner color='blue' onClick={handleChoiceGiftBannerClick}>
                  <GiftWithStarsImage />

                  <div className='top-20'>
                    <Text variant='h3' color='white' center>
                      <FormattedMessage id='home__title' />
                    </Text>
                  </div>

                  <Text variant='button' color='white'>
                    <FormattedMessage id='home__button' />
                  </Text>
                </Banner>
              </div>
            </Slider>
          </SliderWrap>
        </div>
      </Scrollable>

      <SliderDots idx={index + 1} count={items.length + 1} />

      <FooterBar selected='home' />
    </>
  )
}
