import { StoreonModule } from 'storeon'

import { queryUserGet } from '../../../app/http/http'
import { TEvents, TState } from '../../../app/store1'

const profileEvents: StoreonModule<TState, TEvents> = (store) => {
  store.on('on-profile-page-mount', () => store.dispatch('query-user-get'))
}

const profileQueries: StoreonModule<TState, TEvents> = (store) => {
  store.on('query-user-get', async () => {
    const { data } = await queryUserGet()
    if (data.status) {
      const user = data.items[0]
      store.dispatch('set-name', user.name)
      store.dispatch('set-last-name', user.last_name)
      store.dispatch('set-dob', user.birthday)
      store.dispatch('set-email', user.email)
    }
  })
}

export const profileModel: StoreonModule<TState, TEvents>[] = [profileEvents, profileQueries]
