import { StoreonModule } from 'storeon'

import { sendUserEventToGA } from '../../../app/googleAnalytics'
import { TEvents, TState } from '../../../app/store1'

const onboardingEvents: StoreonModule<TState, TEvents> = (store) => {
  store.on('on-onboarding-skip', (state) => {
    sendUserEventToGA('Нажата кнопка пропустить Help')
    if (state.authorized) store.dispatch('set-redirect-link', '/home')
    else store.dispatch('set-redirect-link', '/auth')
  })
  store.on('on-onboarding-got-it', (state) => {
    if (state.authorized) store.dispatch('set-redirect-link', '/home')
    else store.dispatch('set-redirect-link', '/auth')
  })
}

export const onboardingModel: StoreonModule<TState, TEvents>[] = [onboardingEvents]
