import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../app/components'

import { demaskPhoneNum, maskPhoneNum } from '../../../app/utils'

type TProps = {
  name: string
  phone: string
  continueBtnDisabled: boolean
  onBack: () => void
  onNameChange: (val: string) => void
  onPhoneChange: (val: string) => void
  onContinue: () => void
}

export const NewContact: React.FC<TProps> = ({
  name,
  phone,
  continueBtnDisabled,
  onBack,
  onNameChange,
  onPhoneChange,
  onContinue,
}) => {
  const intl = useIntl()

  return (
    <>
      <BackBtn onClick={onBack} />

      <div className='top-60 side-25'>
        <Text variant='h1'>
          <FormattedMessage id='contacts__donee__title' />
        </Text>
      </div>

      <Scrollable>
        <div className='top-35 bottom-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__doneename__label' })}
            closeBtn
            value={name}
            onChange={onNameChange}
          />
        </div>

        <div className='top-35 bottom-20 side-25'>
          <Input
            closeBtn
            variant='phoneMini'
            value={maskPhoneNum(phone)}
            label={intl.formatMessage({ id: 'contacts__doneephone__label' })}
            onChange={(val) => onPhoneChange(demaskPhoneNum(val))}
          />
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button variant='filled' disabled={continueBtnDisabled} onClick={onContinue}>
          <FormattedMessage id='contacts__message__button' />
        </Button>
      </div>
    </>
  )
}
