import React from 'react'
import { Link } from 'react-router-dom'

import { Wrap } from './styled'

type TProps = {
  variant: 'text' | 'rounded' | 'filled' | 'outlined' | 'icon'
  whiteText?: boolean
  fullWidth?: boolean
  to?: string
  onClick?: () => void
  disabled?: boolean
}

export const Button: React.FC<TProps> = ({
  variant,
  whiteText,
  children,
  to,
  fullWidth,
  onClick,
  disabled,
}) => (
  <Wrap variant={variant} fullWidth={fullWidth} whiteText={whiteText} disabled={disabled}>
    {to === undefined ? (
      <button type='button' onClick={onClick} disabled={disabled}>
        {children}
      </button>
    ) : (
      <Link to={to}>{children}</Link>
    )}
  </Wrap>
)
