import React, { useEffect, useState } from 'react'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { prepareCode } from './utils'

export const SmsCodeInput: React.FC = () => {
  const { dispatch, smsCode, prevSmsCode, timer } = useStoreon<TState, TEvents>(
    'smsCode',
    'prevSmsCode',
    'timer'
  )

  const [code, setCode] = useState({ 0: '', 1: '', 2: '', 3: '' })

  useEffect(() => {
    const inputs = document.getElementsByClassName('sms-code-input') as HTMLCollectionOf<
      HTMLInputElement
    >
    const { length } = smsCode
    const isDel = length < prevSmsCode.length
    if (length === 0) inputs[0].focus()
    if (length === 1) inputs[isDel ? 0 : 1].focus()
    if (length === 2) inputs[isDel ? 1 : 2].focus()
    if (length === 3) inputs[isDel ? 2 : 3].focus()
    return () => inputs[3].blur()
  }, [prevSmsCode.length, smsCode])

  useEffect(() => {
    dispatch('on-sms-code-change', prepareCode(code))
  }, [code, dispatch])

  const onChange = (idx: number, val: string) => {
    setCode((prev) => ({ ...prev, [idx]: val }))
  }

  return (
    <div className='top-20 center'>
      <Input
        variant='sms-code'
        value={smsCode[0] ?? ''}
        onChange={(val) => onChange(0, val)}
        disabled={timer === 0}
      />
      <Input
        variant='sms-code'
        value={smsCode[1] ?? ''}
        onChange={(val) => onChange(1, val)}
        disabled={timer === 0}
      />
      <Input
        variant='sms-code'
        value={smsCode[2] ?? ''}
        onChange={(val) => onChange(2, val)}
        disabled={timer === 0}
      />
      <Input
        variant='sms-code'
        value={smsCode[3] ?? ''}
        onChange={(val) => onChange(3, val)}
        disabled={timer === 0}
      />
    </div>
  )
}
