import React, { useEffect, useState } from 'react'

import { FullModal } from '../../app/components'
import { Catalog, Confirmation, Contacts, Details, Message, NewContact, Result } from './components'

import { useQueries } from './api'

import { useSendUserEventToGoogleAnalytics } from '../../app/googleAnalytics'
import { useSharedQueries } from '../../app/http'
import { getPhoneWithCode, getPhoneWithoutCode } from '../../app/utils'

import { TContact, TFullModal, TOutgoingGift, TProduct, TSection } from './types'

export const CatalogFeature: React.FC = () => {
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()
  const { querySentHistory } = useSharedQueries()
  const {
    querySections,
    queryProductsBySectionId,
    queryAddressBook,
    queryGiftAdd,
    queryGiftPayByCard,
  } = useQueries()

  const [products, setProducts] = useState<TProduct[] | null>(null)
  const [currentSection, setCurrentSection] = useState(1)
  const [sections1, setSections1] = useState<TSection[] | null>(null)
  const [fullModal, setFullModal] = useState<TFullModal>(null)
  const [currentProductId, setCurrentProductId] = useState(0)
  const [contacts, setContacts] = useState<TContact[]>([])
  const [currentContactId, setCurrentContactId] = useState(0)
  const [name, setName] = useState('')
  const [phone, setPhone] = useState('')
  const [message, setMessage] = useState('')
  const [resultGift, setResultGift] = useState<TOutgoingGift | null>(null)

  const getProductById = (pId: number, arr: TProduct[]) => arr.find(({ id }) => pId === id)
  const getContactById = (dId: number, arr: TContact[]) => arr.find(({ id }) => dId === id)
  const getOutgoingGiftById = (gId: number, arr: TOutgoingGift[]) =>
    arr.find(({ id }) => gId === id)

  const computePhone = () => {
    const contactPhone = getContactById(currentContactId, contacts)?.phone
    if (contactPhone) return getPhoneWithoutCode(contactPhone)
    return phone
  }

  const validateName = ({ length }: string) => length >= 2 && length <= 50
  const validatePhone = ({ length }: string) => length === 10

  useEffect(() => {
    ;(async () => {
      const { data } = await querySections()
      setSections1(data.items)
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  useEffect(() => {
    if (sections1 !== null) {
      const { id } = sections1[currentSection - 1]
      ;(async () => {
        const { data } = await queryProductsBySectionId(id)
        setProducts(data.items)
      })()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentSection, sections1])

  const handleTabChange = async (current: number, tName: string) => {
    sendUserEvent(`Переход по экранам категорий подарков (${tName})`)
    await setProducts(null)
    setCurrentSection(current)
  }
  const handleProductSelect = async (id: number) => {
    await setCurrentProductId(id)
    setFullModal('details')
  }
  const handleDetailsModalClose = () => {
    setFullModal(null)
  }
  const handleDetailsModalGift = async () => {
    const { data } = await queryAddressBook()
    await setContacts(data.items)
    setFullModal('contacts')
  }
  const handleContactsModalClose = () => {
    setFullModal(null)
  }
  const handleContactsModalSearch = (val: string) => {
    setName(val)
  }
  const handleContactsModalSelect = async (id: number) => {
    sendUserEvent('Выбран получатель подарка')
    await setCurrentContactId(id)
    setFullModal('message')
  }
  const handleContactsModalContinue = async () => {
    sendUserEvent('Переход на страницу создания контакта')
    await setCurrentContactId(0)
    setFullModal('newContact')
  }
  const handleNewContactModalNameChange = (val: string) => {
    setName(val)
  }
  const handleNewContactModalPhoneChange = (val: string) => {
    setPhone(val)
  }
  const handleNewContactModalContinue = () => {
    setFullModal('message')
  }
  const handleMessageModalBack = () => {
    setFullModal('contacts')
  }
  const handleMessageModalInput = (val: string) => {
    setMessage(val)
  }
  const handleMessageModalContinue = () => {
    sendUserEvent('Заполнен текст поздравления')
    setFullModal('confirmation')
  }
  const handleConfirmationModalBack = () => {
    setFullModal('message')
  }
  const handleConfirmationModalNameChange = (val: string) => {
    setName(val)
  }
  const handleConfirmationModalPhoneChange = (val: string) => {
    setPhone(val)
  }
  const handleConfirmationModalMessageChange = (val: string) => {
    setMessage(val)
  }
  const handleConfirmationModalPay = async () => {
    sendUserEvent('Переход на оплату подарка')

    const { data: addGiftData } = await queryGiftAdd({
      name: getContactById(currentContactId, contacts)?.name || name,
      phone: getContactById(currentContactId, contacts)?.phone || getPhoneWithCode(phone),
      product_id: currentProductId,
      message,
    })
    const { id: giftId } = addGiftData.items[0]
    const { data: payData } = await queryGiftPayByCard(giftId)
    if (payData.status) {
      const { data: history } = await querySentHistory()
      const gift = getOutgoingGiftById(giftId, history.items)
      if (gift) await setResultGift(gift)
      setFullModal('result')
      // eslint-disable-next-line no-console
      console.log(history)
    }
  }
  const handleResultModalClose = () => {
    setFullModal(null)
  }

  return (
    <>
      <Catalog
        sections={sections1}
        currentSection={currentSection}
        products={products}
        onTabChange={handleTabChange}
        onProductSelect={handleProductSelect}
      />

      <FullModal isOpen={fullModal !== null}>
        {fullModal === 'details' && (
          <Details
            productId={currentProductId}
            onClose={handleDetailsModalClose}
            onGift={handleDetailsModalGift}
          />
        )}

        {fullModal === 'contacts' && (
          <Contacts
            onClose={handleContactsModalClose}
            contacts={contacts}
            doneeName={name}
            onSearch={handleContactsModalSearch}
            onSelect={handleContactsModalSelect}
            onContinue={handleContactsModalContinue}
          />
        )}

        {fullModal === 'newContact' && (
          <NewContact
            name={name}
            phone={phone}
            continueBtnDisabled={!(validateName(name) && validatePhone(phone))}
            onBack={handleContactsModalClose}
            onNameChange={handleNewContactModalNameChange}
            onPhoneChange={handleNewContactModalPhoneChange}
            onContinue={handleNewContactModalContinue}
          />
        )}

        {fullModal === 'message' && (
          <Message
            message={message}
            onBack={handleMessageModalBack}
            onChange={handleMessageModalInput}
            onContinue={handleMessageModalContinue}
          />
        )}

        {fullModal === 'confirmation' && products !== null && (
          <Confirmation
            giftName={getProductById(currentProductId, products)?.name || ''}
            contactName={getContactById(currentContactId, contacts)?.name || name}
            phone={computePhone()}
            message={message}
            price={getProductById(currentProductId, products)?.price.initial}
            onBack={handleConfirmationModalBack}
            onContactNameChange={handleConfirmationModalNameChange}
            onPhoneChange={handleConfirmationModalPhoneChange}
            onMessageChange={handleConfirmationModalMessageChange}
            onPay={handleConfirmationModalPay}
          />
        )}

        {fullModal === 'result' && resultGift !== null && (
          <Result
            giftName={resultGift.product.items[0].name}
            contactName={resultGift.whom.name}
            phone={resultGift.whom.phone}
            message={resultGift.message !== false ? resultGift.message : ''}
            price={resultGift.product.items[0].price.initial}
            dateAndTime={resultGift.date_created}
            onClose={handleResultModalClose}
          />
        )}
      </FullModal>
    </>
  )
}
