import React from 'react'
import ReactModal from 'react-modal'
import { useTheme } from 'styled-components'

type TProps = {
  isOpen: boolean
}

export const FullModal: React.FC<TProps> = ({ children, isOpen }) => {
  const { small } = useTheme().radius

  return (
    <ReactModal
      isOpen={isOpen}
      style={{
        content: {
          border: 'unset',
          padding: '0',
          width: '100%',
          height: '100%',
          position: 'static',
          overflow: 'hidden',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'stretch',
          justifyContent: 'space-between',
          borderRadius: small,
          boxShadow: '0px 0px 30px rgba(0, 0, 0, 0.05)',
        },
        overlay: {
          padding: 0,
          height: '100%',
        },
      }}
    >
      {children}
    </ReactModal>
  )
}
