import React from 'react'
import { FormattedMessage } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Button } from '..'
import { useAuthDeleteQuery } from '../../http'
import { TEvents, TState } from '../../store1'

import { Wrap } from './styled'

export const LogoutBtn: React.FC = () => {
  const query = useAuthDeleteQuery()

  const { dispatch } = useStoreon<TState, TEvents>()

  /* const {
    dispatch,
    actions: { setAuthAction },
  } = useStore() */

  const handleClick = async () => {
    const { data } = await query()
    if (data.status || !data.status) dispatch('on-logout')
  }

  return (
    <Wrap>
      <Button variant='text' onClick={handleClick}>
        <FormattedMessage id='logout__button' />
      </Button>
    </Wrap>
  )
}
