import React, { FC } from 'react'
import { useStoreon } from 'storeon/react'

import { Button } from '../../..'
import { TEvents, TState } from '../../../../store1'

export const YesNoButtons: FC = () => {
  const { dispatch } = useStoreon<TState, TEvents>()

  return (
    <div className='side-25 center-between'>
      <Button variant='text' onClick={() => dispatch('on-popup-yes')}>
        Да
      </Button>

      <Button variant='text' onClick={() => dispatch('on-popup-no')}>
        Нет
      </Button>
    </div>
  )
}
