import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../../app/components'

import { useSendUserEventToGoogleAnalytics } from '../../../../app/googleAnalytics'
import { useGiftPayByCard } from '../../../../app/http'
import { useStore } from '../../../../app/store'
import { demask, mask } from '../../../auth/utils'

export const Confirmation: React.FC = () => {
  const intl = useIntl()
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()
  /* const redirect = useHistory().push */

  const queryPay = useGiftPayByCard()

  const {
    state: { doneePhone, doneeName, doneeMessage, currentGift },
    dispatch,
    actions: { setDoneeMessage, setDoneeName, setDoneePhone /* setCurrentGift */ },
  } = useStore()

  const placeholder: string = '(___)-___-__-__'
  const handlePhone = (value: string) => {
    const { length: pLength } = placeholder
    const { length: vLength } = value

    if (vLength <= pLength) {
      const demasked = demask(value)

      if (demasked.match(/[^0-9]/g) === null) {
        dispatch(setDoneePhone(mask(demasked, placeholder)))
      }
    }
  }
  const handlePayBtnClick = async () => {
    sendUserEvent('Переход на оплату подарка')

    /* const { data: addData } = await queryGiftAdd({
      name: doneeName,
      last_name: 'Фамилия',
      phone: `7${demask(doneePhone)}`,
      product_id: currentGift.id.toString(),
      message: doneeMessage,
    }) */
    // eslint-disable-next-line no-console
    /* console.log(addData) */

    const { data: payData } = await queryPay(currentGift.id)
    // eslint-disable-next-line no-console
    console.log(payData)
    /* redirect('/gift/success') */
  }

  return (
    <>
      <div className='top-60'>
        <BackBtn />
      </div>

      <Scrollable>
        <div className='side-25'>
          <Text variant='h1'>
            <FormattedMessage id='contacts__confirmation__title' />
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__1' })}
            value={currentGift.name}
            disabled
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__2' })}
            onChange={(val) => dispatch(setDoneeName(val))}
            value={doneeName}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__3' })}
            onChange={handlePhone}
            variant='phoneMini'
            value={doneePhone}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__4' })}
            onChange={(val) => dispatch(setDoneeMessage(val))}
            value={doneeMessage}
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__confirmation__label__5' })}
            value={`${currentGift.price}`}
            disabled
          />
        </div>

        <div className='top-35 side-25'>
          <Text variant='h3' color='black'>
            <FormattedMessage id='contacts__confirmation__subtitle' />
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Text variant='field' color='neutral'>
            {`Мы зарезервируем ${currentGift.price} ₽  на вашей карте и  спишем после получения подарка`}
          </Text>
        </div>
      </Scrollable>

      <div className='top-20 bottom-20 side-25'>
        <Button
          variant='filled'
          onClick={handlePayBtnClick}
        >{`Оплатить ${currentGift.price} ₽`}</Button>
      </div>
    </>
  )
}
