import styled, { keyframes } from 'styled-components'

const pulse = keyframes`
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.1);
  }

  75% {
    transform: scale(0.9);
  }

  100% {
    transform: scale(1);
  }
`

export const Wrap = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;

  > div {
    position: absolute;
    top: calc(42vh - (75px / 2));
    left: calc(50% - (200px / 2));
    z-index: 1;
    animation: ${pulse} 2s linear infinite;
  }

  > svg:nth-child(2) {
    position: absolute;
    top: calc(17vh - (130px / 2));
    left: 65px;
  }

  > svg:nth-child(3) {
    position: absolute;
    top: calc(56vh - (180px / 2));
    left: -70px;
  }

  > svg:nth-child(4) {
    position: absolute;
    top: calc(69vh - (240px / 2));
    right: -45px;
  }

  @media (min-width: 600px) {
    > svg:nth-child(2) {
      left: 130px;
    }

    > svg:nth-child(3) {
      left: 0;
    }

    > svg:nth-child(4) {
      right: 0;
    }
  }
`
