import React, { Fragment, useEffect, useState } from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { useHistory } from 'react-router-dom'

import { Button, CloseBtn, ContactsItem, Input, Scrollable, Text } from '../../../../app/components'

import { useSendUserEventToGoogleAnalytics } from '../../../../app/googleAnalytics'
import { useStore } from '../../../../app/store'
import { Http } from '../../../../app/http/services'

type STypes = {
  id: number
  phone: string
  name: string
  photo: boolean | string
}

export const Contacts: React.FC = () => {
  const intl = useIntl()
  const history = useHistory()
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()

  const {
    state: { doneeName },
    dispatch,
    actions: { setDoneeName },
  } = useStore()

  const [contacts, setContacts] = useState<STypes[]>([])

  useEffect(() => {
    ;(async () => {
      const {
        data: { items },
      } = await Http.get('/user/address_book/')
      setContacts(items)
    })()
  }, [])

  return (
    <>
      <div className='top-60'>
        <CloseBtn />
      </div>
      <Scrollable>
        <div className='side-25'>
          <Text variant='h1'>
            <FormattedMessage id='contacts__title' />
          </Text>
        </div>
        <div className='top-35 bottom-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__label' })}
            closeBtn
            value={doneeName}
            onChange={(val) => dispatch(setDoneeName(val))}
          />
        </div>
        <div className='side-25'>
          {contacts.filter(({ name }) => name.includes(doneeName)).length
            ? contacts
                .sort((a, b) => {
                  if (a.name < b.name) return -1
                  if (a.name > b.name) return 1
                  return 0
                })
                .filter(({ name }) => name.includes(doneeName))
                .map((item, index, arr) => {
                  const { name, phone, photo, id } = item
                  const isNewLetter =
                    index === 0 ||
                    name.substr(0, 1).toLowerCase() !==
                      arr[index - 1].name.substr(0, 1).toLowerCase()
                  return (
                    <Fragment key={id}>
                      {isNewLetter && (
                        <div className='bottom-20'>
                          <Text variant='h4'>{name.substr(0, 1)}</Text>
                        </div>
                      )}
                      <ContactsItem name={name} phone={phone} photo={photo} />
                    </Fragment>
                  )
                })
            : 'Контактов не найдено'}
        </div>
      </Scrollable>
      <div className='top-20 bottom-20 side-25'>
        <Button
          variant='filled'
          disabled={doneeName === ''}
          onClick={() => {
            sendUserEvent('')
            history.push('/gift/donee')
          }}
        >
          <FormattedMessage id='contacts__button' />
        </Button>
      </div>
    </>
  )
}
