import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { pipe } from 'ramda'

import { CloseBtn, Input, Scrollable, Text } from '../../../../app/components'
import { SuccessIcon } from '../../../../app/components/icons'

import { getPhoneWithoutCode, maskPhoneNum } from '../../../../app/utils'

import { TIncomingGift } from '../../../../app/types'

type TProps = {
  gift: TIncomingGift
  on: (e: 'close') => void
}

export const Processing: React.FC<TProps> = ({ gift, on }) => {
  const intl = useIntl()
  const transformPhoneNum = pipe(getPhoneWithoutCode, maskPhoneNum)
  const {
    product: {
      items: {
        0: { name: giftName },
      },
    },
    whom: { name: whomName, phone },
    // eslint-disable-next-line @typescript-eslint/naming-convention
    date_send,
  } = gift

  return (
    <>
      <CloseBtn onClick={() => on('close')} />

      <Scrollable>
        <div className='top-60 center'>
          <SuccessIcon />
        </div>

        <div className='top-20 side-25'>
          <Text variant='h1' color='purple' center>
            <FormattedMessage id='receive__processing__title' />
          </Text>
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'receive__processing__label__1' })}
            value={giftName}
            disabled
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'receive__processing__label__2' })}
            value={whomName}
            disabled
          />
        </div>

        <div className='top-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'receive__processing__label__3' })}
            value={transformPhoneNum(phone)}
            disabled
          />
        </div>

        <div className='top-20 side-25 bottom-20'>
          <Input
            label={intl.formatMessage({ id: 'receive__processing__label__4' })}
            value={date_send}
            disabled
          />
        </div>
      </Scrollable>
    </>
  )
}
