import { convertObjectToFormData, query } from '../../../../app/http'

const giftChoice = 'gift/choice/'

const queryMoney = (giftId: number) =>
  query(
    'post',
    giftChoice,
    undefined,
    convertObjectToFormData({
      id: giftId,
      choice_code: 'money-card',
      payment_system: 'mandarinpay',
    })
  )

const queryGift = (giftId: number) =>
  query(
    'post',
    giftChoice,
    undefined,
    convertObjectToFormData({
      id: giftId,
      choice_code: 'delivery',
    })
  )

export const useQueries = () => ({ queryMoney, queryGift })
