import React from 'react'
// @ts-ignore
import Slider from 'react-slick'

import { Wrap } from './styled'

import 'slick-carousel/slick/slick.css'

type TProps = {
  images: any
  dotsColor?: 'blue' | 'orange'
}

export const SliderDefault: React.FC<TProps> = ({ images, dotsColor = 'blue' }) => {
  const settings = {
    dotsClass: `slick-dots ${dotsColor}`,
    dots: true,
    arrows: false,
    slidesToShow: 1,
  }

  return (
    <Wrap>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <Slider {...settings}>
        {images.map((item: any) => (
          <img src={item?.image} alt={item?.alt} key={item?.id} />
        ))}
      </Slider>
    </Wrap>
  )
}
