import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import { nanoid } from 'nanoid'

import { Auth, CatalogFeature, Gift, History, Home, Onboarding, Profile } from '../../features'

export const Routes: React.FC = () => (
  <Switch>
    <Route key={nanoid()} path='/onboarding' component={Onboarding} />
    <Route key={nanoid()} path='/auth' component={Auth} />
    <Route key={nanoid()} path='/home' component={Home} />
    <Route key={nanoid()} path='/profile' component={Profile} />
    <Route key={nanoid()} path='/history' component={History} />
    <Route key={nanoid()} path='/catalog' component={CatalogFeature} />
    <Route key={nanoid()} path='/gift' component={Gift} />
    <Route key={nanoid()} path='/'>
      <Redirect to='/onboarding' />
    </Route>
  </Switch>
)
