import styled from 'styled-components'

export const ImageWrap = styled.div`
  > img {
    width: 100%;
    height: 40vh;
    object-fit: cover;
  }
`

export const Message = styled.div`
  width: 100%;
  padding: 15px 25px;
  background: ${({ theme }) => theme.color.message};
  border-radius: ${({ theme }) => theme.radius.normal} ${({ theme }) => theme.radius.normal}
    ${({ theme }) => theme.radius.normal} 0;
`
