import ReactGA from 'react-ga'

const [trackingCode, category] = ['G-B8DGQQEYL3', 'Действия пользователя']

export const initGoogleAnalytics = () => ReactGA.initialize(trackingCode)

export const useSendUserEventToGoogleAnalytics = () => ({
  sendUserEvent: (action: string) =>
    ReactGA.event({
      category,
      action,
    }),
})

export const sendUserEventToGA = (action: string) => ReactGA.event({ category, action })
