import React, { FC } from 'react'
import { useIntl } from 'react-intl'
import { useStoreon } from 'storeon/react'

import { Input } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { formatDOBToModel, formatDOBToView } from '../../../../../../app/utils'

export const DOBInput: FC = () => {
  const intl = useIntl()

  const { dispatch, DOB } = useStoreon<TState, TEvents>('DOB')

  return (
    <div className='top-20 side-25'>
      <Input
        label={intl.formatMessage({ id: 'auth__profile__label__2' })}
        onChange={(val) => dispatch('on-dob-change', formatDOBToModel(val))}
        value={formatDOBToView(DOB)}
        variant='date'
      />
    </div>
  )
}
