import React from 'react'
import { FormattedMessage } from 'react-intl'
import { pipe } from 'ramda'
import { useStoreon } from 'storeon/react'

import { Text } from '../../../../../../app/components'

import { TEvents, TState } from '../../../../../../app/store1'
import { getPhoneWithoutCode, maskPhoneNum } from '../../../../../../app/utils'

export const Description: React.FC = () => {
  const { phone } = useStoreon<TState, TEvents>('phone')

  return (
    <div className='top-20 side-25'>
      <Text variant='description' center>
        <FormattedMessage
          id='auth__code__description'
          values={{ phone: pipe(getPhoneWithoutCode, maskPhoneNum)(String(phone)) }}
        />
      </Text>
    </div>
  )
}
