import React, { FC } from 'react'

import { Dots } from '../../../../app/components'

type TProps = {
  idx: number
  count: number
}

export const SliderDots: FC<TProps> = ({ idx, count }) => (
  <div className='top-20 bottom-20 center'>
    <Dots selected={idx} color='orange' dotsCount={count} />
  </div>
)
