import React from 'react'

import { TColor, TText } from '../../theme'

import { Wrap } from './styled'

type TProps = {
  variant: TText
  color?: TColor
  center?: boolean
}

export const Text: React.FC<TProps> = ({ children, variant, color = 'black', center }) => {
  const computeTag = (byVariant: string) => {
    if (byVariant === 'h1') return byVariant
    if (byVariant === 'h3') return byVariant
    return 'div'
  }

  return (
    <Wrap as={computeTag(variant)} variant={variant} color={color} center={center}>
      {children}
    </Wrap>
  )
}
