import { phoneCode } from '../constants'

export const maskPhoneNum = (phoneNum: string): string => {
  const { length } = phoneNum
  const placeholder = '(___)-___-__-__'

  const splitPlaceholder: string[] = placeholder.split('')
  const splitPhoneNum: string[] = phoneNum.split('')

  let i = 0

  const masked = splitPlaceholder
    .map((char) => {
      if (char.match(/[() -]/i) !== null) return i < length ? char : ''
      if (i < length) {
        const newChar = splitPhoneNum[i]
        i += 1
        return newChar
      }
      return ''
    })
    .join('')

  return masked !== placeholder ? masked : ''
}

export const demaskPhoneNum = (masked: string): string => masked.replace(/[()_-]/gi, '')

export const getPhoneWithoutCode = (phoneNum: string) => phoneNum.substring(1)

export const getPhoneWithCode = (phoneNum: string) => `${phoneCode}${phoneNum}`

export const validatePhoneInput = (phone: string): boolean =>
  phone.length <= 11 && phone.match(/[^0-9]/g) === null

export const validatePhone = (phone: string): boolean => phone.length === 11

export const validateSmsCodeInput = (code: string): boolean =>
  code.length <= 4 && code.match(/[^0-9_]/g) === null

export const validateSmsCode = (code: string) => code.length === 4 && code.match(/[^0-9]/g) === null

export const validateAnyNameInput = (val: string) => val.length <= 50

export const validateAnyName = (val: string) => val.length >= 2 && val.length <= 50

export const formatDOBToModel = (val: string) => val.replaceAll('-', '/')

export const formatDOBToView = (val: string) => val.replaceAll('/', '-')

export const validateRequired = (val: string) => val.length !== 0

export const validateEmail = (val: string) => val.match(/[\w.-]+@[\w-]+\.[\w.-]*[\w^_]$/gi) !== null
