import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useTheme } from 'styled-components'

import { Button } from '..'
import { useSendUserEventToGoogleAnalytics } from '../../googleAnalytics'
import { useSharedQueries } from '../../http'
import { useStore } from '../../store'
import { Gift, History, Home, Profile } from './icons'

import { Badge, Wrap } from './styled'

type TTab = 'home' | 'gifts' | 'history' | 'profile'
type TProps = {
  selected: TTab
}

export const FooterBar: React.FC<TProps> = ({ selected }) => {
  const { queryNewIncomingGifts } = useSharedQueries()
  const { gray2, blue } = useTheme().color
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()
  const redirect = useHistory().push

  const computeColor = (tab: TTab) => (tab === selected ? blue : gray2)

  const {
    state: {
      authorized,
      newIncomingGifts: { count },
    },
    dispatch,
    actions: { setNewIncomingGifts },
  } = useStore()

  useEffect(() => {
    if (authorized) {
      ;(async () => {
        const { data } = await queryNewIncomingGifts()
        if (data.status) dispatch(setNewIncomingGifts(data.items, data.items_count))
        // eslint-disable-next-line no-console
        console.log(data.items)
      })()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [authorized, count])

  const handleBarBtnClick = (route: string) => {
    sendUserEvent(`Переход на экран "${route}"`)
    redirect(route)
  }

  return (
    <Wrap>
      <Button variant='icon' onClick={() => handleBarBtnClick('/home')}>
        <Badge count={count} color={computeColor('home')} />

        <Home color={computeColor('home')} />
      </Button>

      <Button variant='icon' onClick={() => handleBarBtnClick('/catalog')}>
        <Gift color={computeColor('gifts')} />
      </Button>

      <Button variant='icon' onClick={() => handleBarBtnClick('/history')}>
        <History color={computeColor('history')} />
      </Button>

      <Button variant='icon' onClick={() => handleBarBtnClick('/profile')}>
        <Profile color={computeColor('profile')} />
      </Button>
    </Wrap>
  )
}
