import React, { createContext, Dispatch, useContext, useReducer } from 'react'

import { removeToken, setToken } from '../http'

import { TIncomingGift } from '../types'

type TState = {
  authorized: boolean
  phone: string
  newIncomingGifts: { items: TIncomingGift[]; count: number }
  doneeName: string
  doneePhone: string
  doneeMessage: string
  currentGift: {
    price: number | string
    name: string
    id: number
  }
  isAboutModal: boolean
}

type TAction =
  | { type: 'set-auth'; isAuth: boolean; token?: string }
  | { type: 'set-phone'; phone: string }
  | { type: 'set-new-incoming-gifts'; items: TIncomingGift[]; count: number }
  | { type: 'set-doneePhone'; payload: string }
  | { type: 'set-doneeName'; payload: string }
  | { type: 'set-doneeMessage'; payload: string }
  | {
      type: 'set-currentGift'
      payload: {
        price: number | string
        name: string
        id: number
      }
    }
  | { type: 'set-is-about-modal'; isAboutModal: boolean }

const initialState: TState = {
  authorized: true,
  phone: '',
  doneeName: '',
  doneePhone: '',
  doneeMessage: '',
  currentGift: {
    price: '',
    name: '',
    id: 0,
  },
  newIncomingGifts: { items: [], count: 0 },
  isAboutModal: false,
}

const StoreContext = createContext<TState>(initialState)
const DispatchContext = createContext({} as Dispatch<TAction>)

const setAuthAction = (isAuth: boolean, token?: string): TAction => ({
  type: 'set-auth',
  isAuth,
  token,
})
const setPhoneAction = (phone: string): TAction => ({ type: 'set-phone', phone })
const setDoneeName = (payload: string): TAction => ({ type: 'set-doneeName', payload })
const setDoneePhone = (payload: string): TAction => ({ type: 'set-doneePhone', payload })
const setDoneeMessage = (payload: string): TAction => ({ type: 'set-doneeMessage', payload })
const setCurrentGift = (payload: {
  price: number | string
  name: string
  id: number
}): TAction => ({
  type: 'set-currentGift',
  payload,
})
const setNewIncomingGifts = (items: TIncomingGift[], count: number): TAction => ({
  type: 'set-new-incoming-gifts',
  items,
  count,
})
const setIsAboutModal = (isAboutModal: boolean): TAction => ({
  type: 'set-is-about-modal',
  isAboutModal,
})

const reducer = (state: TState, action: TAction): TState => {
  switch (action.type) {
    case 'set-auth':
      if (action.token) setToken(action.token)
      if (!action.token) removeToken()
      return { ...state, authorized: action.isAuth }
    case 'set-phone':
      return { ...state, phone: action.phone }
    case 'set-new-incoming-gifts':
      return { ...state, newIncomingGifts: { items: action.items, count: action.count } }
    case 'set-doneePhone':
      return { ...state, doneePhone: action.payload }
    case 'set-doneeName':
      return { ...state, doneeName: action.payload }
    case 'set-doneeMessage':
      return { ...state, doneeMessage: action.payload }
    case 'set-currentGift':
      return { ...state, currentGift: action.payload }
    case 'set-is-about-modal':
      return { ...state, isAboutModal: action.isAboutModal }
    default:
      throw new Error()
  }
}

export const StoreProvider: React.FC = ({ children }) => {
  const [store, dispatch] = useReducer(reducer, initialState)

  return (
    <DispatchContext.Provider value={dispatch}>
      <StoreContext.Provider value={store}>{children}</StoreContext.Provider>
    </DispatchContext.Provider>
  )
}

export const useStore = () => ({
  state: useContext(StoreContext),
  dispatch: useContext(DispatchContext),
  actions: {
    setAuthAction,
    setPhoneAction,
    setDoneeName,
    setDoneePhone,
    setDoneeMessage,
    setCurrentGift,
    setNewIncomingGifts,
    setIsAboutModal,
  },
})
