import React from 'react'
import { FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom'

import { Tab, Wrap } from './styled'

type TProps = {
  selected: number
  color: 'black'
}

export const Tabs: React.FC<TProps> = ({ selected, color }) => (
  <Wrap selected={selected} color={color}>
    <Tab>
      <Link to='/history'>
        <FormattedMessage id='tab__text__1' />
      </Link>
    </Tab>
    <Tab>
      <Link to='/history/sent'>
        <FormattedMessage id='tab__text__2' />
      </Link>
    </Tab>
  </Wrap>
)
