import React from 'react'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'

import { Text } from '..'
import { useSendUserEventToGoogleAnalytics } from '../../googleAnalytics'
import { useStore } from '../../store'
import contacts_item_image from '../images/contacts_item_1.jpg'

const Wrap = styled.button.attrs({ type: 'button' })`
  display: flex;
  width: 100%;
  margin-bottom: 25px;

  .contacts-item-image {
    display: flex;
    width: 50px;
    min-width: 50px;
    height: 50px;
    margin-right: 10px;
  }

  .contacts-item-description {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 10px;
    border-bottom: 1px solid #f2f2f2;

    .contacts-item-title {
      margin-bottom: 10px;
    }
  }
`

type TProps = {
  photo: string | boolean
  phone: string
  name: string
  onClick?: () => void
}

export const ContactsItem: React.FC<TProps> = ({ photo, name, phone, onClick }) => {
  const {
    dispatch,
    actions: { setDoneeName, setDoneePhone },
  } = useStore()
  const { push } = useHistory()
  const { sendUserEvent } = useSendUserEventToGoogleAnalytics()

  return (
    <Wrap onClick={onClick}>
      <div className='contacts-item-image'>
        {/* eslint-disable-next-line */}
        <div
          style={{ cursor: 'pointer' }}
          onClick={() => {
            sendUserEvent('Выбран получатель подарка”')
            dispatch(setDoneeName(name))
            dispatch(setDoneePhone(phone))
            push('/gift/message')
          }}
        >
          <img src={(photo as string) || contacts_item_image} alt='' />
        </div>
      </div>

      <div className='contacts-item-description right-side-25'>
        <div className='contacts-item-title'>
          <Text variant='title'>{name}</Text>
        </div>

        <div>
          <Text variant='description' color='gray3'>
            {phone}
          </Text>
        </div>
      </div>
    </Wrap>
  )
}
