import React from 'react'
import { FormattedMessage, useIntl } from 'react-intl'

import { BackBtn, Button, Input, Scrollable, Text } from '../../../../app/components'

import { useStore } from '../../../../app/store'
import { demask, mask } from '../../../auth/utils'

export const DoneeData: React.FC = () => {
  const intl = useIntl()
  const {
    state: { doneeName, doneePhone },
    dispatch,
    actions: { setDoneeName, setDoneePhone },
  } = useStore()
  const placeholder: string = '(___)-___-__-__'
  const handlePhone = (value: string) => {
    const { length: pLength } = placeholder
    const { length: vLength } = value

    if (vLength <= pLength) {
      const demasked = demask(value)

      if (demasked.match(/[^0-9]/g) === null) {
        dispatch(setDoneePhone(mask(demasked, placeholder)))
      }
    }
  }
  return (
    <>
      <div className='top-60'>
        <BackBtn />
      </div>
      <Scrollable>
        <div className='side-25'>
          <Text variant='h1'>
            <FormattedMessage id='contacts__donee__title' />
          </Text>
        </div>
        <div className='top-35 bottom-20 side-25'>
          <Input
            label={intl.formatMessage({ id: 'contacts__doneename__label' })}
            closeBtn
            value={doneeName}
            onChange={(val) => dispatch(setDoneeName(val))}
          />
        </div>
        <div className='top-35 bottom-20 side-25'>
          <Input
            closeBtn
            variant='phoneMini'
            value={doneePhone}
            label={intl.formatMessage({ id: 'contacts__doneephone__label' })}
            onChange={handlePhone}
          />
        </div>
      </Scrollable>
      <div className='top-20 bottom-20 side-25'>
        <Button
          variant='filled'
          to='/gift/message'
          disabled={!(doneeName.length && demask(doneePhone).length === 10)}
        >
          <FormattedMessage id='contacts__message__button' />
        </Button>
      </div>
    </>
  )
}
