import React from 'react'
import { FormattedMessage } from 'react-intl'

import {
  AboutBtn,
  CatalogItem1,
  CatalogTabs1,
  FooterBar,
  Loader,
  Scrollable,
  Text,
} from '../../../app/components'

import { TProduct, TSection } from '../types'

type TProps = {
  sections: TSection[] | null
  currentSection: number
  products: TProduct[] | null
  onTabChange: (current: number, name: string) => void
  onProductSelect: (id: number) => void
}

export const Catalog: React.FC<TProps> = ({
  sections,
  currentSection,
  products,
  onTabChange,
  onProductSelect,
}) => {
  return (
    <>
      <AboutBtn />

      <div className='top-60 side-25'>
        <Text variant='h1'>
          <FormattedMessage id='catalog__title' />
        </Text>
      </div>

      <div className='top-35 bottom-20 side-25'>
        <Text variant='h4'>
          {sections !== null && (
            <CatalogTabs1 sections={sections} current={currentSection} onChange={onTabChange} />
          )}
        </Text>
      </div>

      <Scrollable>
        <div className='top-35 side-25'>
          {products === null ? (
            <Loader />
          ) : (
            products.map(({ id, name, price: { initial }, picture }) => (
              <CatalogItem1
                key={id}
                name={name}
                price={initial}
                img={picture}
                onClick={() => onProductSelect(id)}
              />
            ))
          )}
        </div>
      </Scrollable>

      <FooterBar selected='gifts' />
    </>
  )
}
