import React from 'react'

import { Text } from '..'

import { FormatPrice } from '../../utils'

import { Span, Wrap } from './styled'

type TProps = {
  price: number
  img: string
  name: string
  onClick: () => void
}

export const CatalogItem1: React.FC<TProps> = ({ name, price, img, onClick }) => (
  <Wrap onClick={onClick} type='button'>
    <div className='catalog-item-image'>
      <img src={img} alt='' />
    </div>

    <div className='catalog-item-description right-side-25'>
      <div className='catalog-item-title bottom-20'>
        <Text variant='title'>{name}</Text>
      </div>

      <div className='catalog-item-price'>
        <Text variant='title' color='purple'>
          {FormatPrice(price)}
          <Span color='purple'>₽</Span>
        </Text>
      </div>
    </div>
  </Wrap>
)
