import React, { Fragment, useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import dayjs from 'dayjs'
import isToday from 'dayjs/plugin/isToday'
import localizedFormat from 'dayjs/plugin/localizedFormat'

import {
  AboutBtn,
  FooterBar,
  HistoryItem,
  Loader,
  Scrollable,
  Tabs,
  Text,
} from '../../../../app/components'

import { Http } from '../../../../app/http/services'

dayjs.extend(isToday)
dayjs.extend(localizedFormat)

export const Received: React.FC = () => {
  const [recivedGifts, setRecivedGifts] = useState({
    items: [],
    isLoading: true,
  })
  useEffect(() => {
    ;(async () => {
      const {
        data: { items },
      } = await Http.get('/gift/incoming/', { page: 1 })
      setRecivedGifts({ items, isLoading: false })
    })()
  }, [])
  return (
    <>
      <Scrollable>
        <AboutBtn />
        <div className='top-20 center'>
          <Text variant='title'>
            <FormattedMessage id='history__received__subtitle' />
          </Text>
        </div>
        <div className='top-20 bottom-20 side-25'>
          <Text variant='h4'>
            <Tabs selected={1} color='black' />
          </Text>
        </div>
        <div className='side-25'>
          {recivedGifts.isLoading && <Loader />}
          {!recivedGifts.isLoading &&
            recivedGifts.items.length &&
            recivedGifts.items.map(
              (
                {
                  message,
                  price: { amount_display },
                  who: { name },
                  id,
                  product: { items },
                  date_created,
                },
                index,
                arr
              ) => {
                let isDayChanged = false
                if (index !== 0) {
                  const { date_created: prevDate } = arr[index - 1]
                  if (
                    dayjs(date_created).startOf('day').format() !==
                    dayjs(prevDate).startOf('day').format()
                  ) {
                    isDayChanged = true
                  }
                }
                const { name: productName, picture } = items[0]
                return (
                  <Fragment key={id}>
                    {(isDayChanged || index === 0) && (
                      <div className='top-20 bottom-20'>
                        <Text variant='h4'>
                          {dayjs(date_created).isToday() ? (
                            <FormattedMessage id='history__received__title' />
                          ) : (
                            dayjs(date_created).locale('ru').format('LL')
                          )}
                        </Text>
                      </div>
                    )}
                    <HistoryItem
                      giftName={productName}
                      message={message}
                      senderName={name}
                      giftPrice={amount_display}
                      img={picture}
                    />
                  </Fragment>
                )
              }
            )}
          {!recivedGifts.isLoading && !recivedGifts.items.length && (
            <>
              <div className='top-35 side-25'>
                <Text variant='h3'>
                  <FormattedMessage id='history__subtitle' />
                </Text>
              </div>
              <div className='top-35 side-25'>
                <Text variant='description'>
                  <FormattedMessage id='history__description' />
                </Text>
              </div>
            </>
          )}
        </div>
      </Scrollable>
      <FooterBar selected='history' />
    </>
  )
}
