import axios from 'axios'
import { sha256 } from 'js-sha256'

import { appId, appSecret } from './constants'
import { baseURL } from './urls'

export const generateAppSign = () => {
  const time = Math.floor(Date.now() / 1000)
  const microTime = time + 0.123456
  const rand = Math.round(Math.random() * 1e12)
  const reqId = `${time}_${microTime}_${rand}`
  const toHash = `${appId}-${reqId}-${appSecret}`
  const hash = sha256(toHash)

  return `${appId}-${hash}-${reqId}`
}

export const setToken = (token: string) => localStorage.setItem('token', token)
export const getToken = () => localStorage.getItem('token')
export const removeToken = () => localStorage.removeItem('token')

const headers = () => ({
  'app-sign': generateAppSign(),
  ...(getToken() && { 'access-token': getToken() }),
})

type TConfigParams = {
  method: 'get' | 'post'
  url: string
  params?: { [key: string]: string }
  data?: { [key: string]: string | { [key: string]: string } } | FormData
}

const config = (params: TConfigParams) => ({
  ...params,
  baseURL,
  headers: headers(),
})

export const query = (
  method: 'get' | 'post',
  url: string,
  params?: { [key: string]: string },
  data?: { [key: string]: string } | FormData
) =>
  axios(
    config({
      method,
      url,
      params,
      data,
    })
  )

export const queryWithNoBaseUrl = (
  method: 'get' | 'post',
  url: string,
  params?: { [key: string]: string },
  data?: { [key: string]: string | { [key: string]: string } } | FormData
) =>
  axios({
    method,
    url,
    params,
    data,
  })

export const convertObjectToFormData = (obj: { [key: string]: string | number }) => {
  const fd = new FormData()
  Object.keys(obj).forEach((key) => fd.append(key, String(obj[key])))
  return fd
}
